import React, { useState, useEffect } from 'react'
import Sidebar from '../CustomNav'
import axios from 'axios'
import PersonaCard from './PersonaCard'
import Logoprofile from '../Additional/Logoprofile'

function empty(){
  return(
    <div className="bg-white rounded-[10px] xsm:border-none h-[65vh] xsm:bg-white md:my-6 my-3 flex flex-col justify-center items-center">
      <div className='flex flex-col gap-2 justify-center items-center'>
        <h2 className="custom-h3-style md:text-[24px] text-[18px] leading-[36px] mt-8">{"Add "}
        <span className='lowercase'>a</span> 
        {" Persona"}
        </h2>
        <p className='custom-seconday-p-style md:text-[16px] text-[14px] text-center'>Add a persona to teach the AI how to speak in your voice.</p>
      </div>
    </div>
  )
}

function Persona() {
  const [personaExists, setPersonaExists] = useState(false)
  const [checkedItems, setCheckedItems] = useState([]);
  const [personas, setPersonas] = useState([])

  useEffect(() => {
    const getSavedPersonas = async () => {
      try {
        const token = localStorage.getItem('token');
        const response = await axios.get('http://localhost:3000/api/getpersona', {
          headers: {
            'Accept': 'application/json, text/plain, */*',
            'Authorization': token
          }
        });
        if(response.status === 400){
          localStorage.removeItem('token');
          window.location.reload();
        }
        setPersonas(response.data.message)
        if (response.data.message.length > 0) {
          setPersonaExists(true)
        }
      } catch (error) {
        localStorage.removeItem('token');
        window.location.reload();
        console.log(error)
      }
    }
    getSavedPersonas()
  }, [])

  const notempty = () => {
    return (
      <>
        <table className="w-full text-sm text-left bg-white rounded-xl">
          <thead className='font-poppins text-base text-gray_dark w-8 border-b border-gray-400 flex-row'>
            <tr className="border-b">
              <th className="py-2 px-4 text-left w-12">
                <input type="checkbox" className="form-checkbox h-5 w-5 text-indigo-600" />
              </th>
              <th className="py-2 px-4 text-left font-medium text-gray-600">Name</th>
              <th className="py-2 px-4 text-left font-medium text-gray-600">Persona Type</th>
              <th className="py-2 px-4 text-right font-medium text-gray-600">Actions</th>
            </tr>
          </thead>
          <tbody>
            {personas.map((data) => (
              <PersonaCard 
                key={data.id} 
                id={data.id}
                persona_name={data.persona_name} 
                tags={data.tags}
                isChecked={checkedItems.includes(data.id)} // Pass the handleDelete function
              />
            ))}
          </tbody>
        <tr className="padding: 0px;">
          <td colSpan={4} className="padding: 0px;">
          <div className='flex items-center justify-between p-2 pl-3 border-t border-gray_dark'>
          <div className="flex items-center">
            <button className="border rounded px-2 py-1 mr-1">&lt;</button>
            <span className="mx-2">1</span>
            <button className="border rounded px-2 py-1 ml-1">&gt;</button>
          </div>
          <div className="flex items-center">
            <span className="mr-2">Rows per page:</span>
            <select className="border rounded px-2 py-1">
              <option>10</option>
            </select>
          </div>
          </div>
          </td>
        </tr>
        </table>
      </>
    )
  }

  return (
    <div className="flex h-full bg-[#e2e8f0] min-h-screen">
      <Sidebar className="w-1/4 p-4" />
      <div className="w-4/5 mx-auto px-4">
        <Logoprofile/>
        <div className='md:pt-[0px] md:min-w-full h-full max-h-full '>
          <div className="max-w-7xl mx-auto p-6">
          <h1 className="text-4xl font-bold mb-2">Personas</h1>
          <p className="text-gray-600 mb-6">
            Use Personas to help quickAI know who you are, who they are speaking as when they generate posts, comments, and more.
          </p>

          {/* <div className="flex justify-between mb-6"> */}
          <div className="flex justify-start items-center md:mt-8 mt-5 ">
            <div className='w-full flex justify-between'>
              <div className="relative">
                <select className="appearance-none bg-white border border-gray-300 rounded-md py-2 pl-3 pr-8 leading-tight focus:outline-none focus:border-blue-500">
                  <option>Order By</option>
                </select>
                <div className="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700">
                  <svg className="fill-current h-4 w-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20">
                    <path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z" />
                  </svg>
                </div>
              </div>
              <div className="flex space-x-2">
                <button className=" bg-blue-600 text-white px-4 py-2 rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2">
                  <a className='no-underline hover:no-underline text-white font-semibold' href='/persona-add'>+ Add Persona</a>
                </button>
              </div>
            </div>
          </div>

          <div className='mt-4'>
            {personaExists ? notempty() : empty()}
          </div>
        </div>
      </div>
      </div>
    </div>
  )
}

export default Persona;
