import React, { useState, useEffect } from 'react';
import axios from 'axios';

const IdeaGenerator = () => {
  const [topics, setTopics] = useState('');
  const [Personas, setPersona] = useState([]);
  const [loading, setLoading] = useState(false);
  const [generatedIdeas, setGeneratedIdeas] = useState('');

  async function getIdea() {
    let token = localStorage.getItem('token');
    setLoading(true);
    try {
      // const response = await axios.post("http://localhost:3000/api/gemni",
      const response = await axios.post("/http://localhost:3000/api/gemni",
        {
          prompt: topics + " dont give response in markdown."
        },
        {
          headers: {
            'Accept': 'application/json, text/plain, */*',
            'Authorization': token
          }
        }
      );
      setGeneratedIdeas(response.data.text);
    } catch (error) {
      console.error("Error generating ideas:", error);
    } finally {
      setLoading(false);
    }
  }

  function copyToClipboard() {
    navigator.clipboard.writeText(generatedIdeas).then(() => {
      alert("Ideas copied to clipboard!");
    }, (err) => {
      console.error('Could not copy text: ', err);
    });
  }

  useEffect(() => {
    async function getpersona() {
      try {
        const token = localStorage.getItem('token');
        // const response = await axios.get('http://localhost:3000/api/getpersona', {
        const response = await axios.get('http://localhost:3000/api/getpersona', {
          headers: {
            'Accept': 'application/json, text/plain, */*',
            'Authorization': token
          }
        });
        setPersona(response.data.message);
      } catch (error) {
        console.error(error);
      }
    }
    getpersona();
  }, []);

  return (
    <div className="xsm:rounded-[16px] xsm:py-2 md:px-0 xsm:mb-2 xsm:px-0 h-full">
      <h1 className="text-3xl font-bold mb-2">Idea Generator</h1>
      <p className="text-gray-600 mb-6">Generate ideas for your LinkedIn posts</p>
      <div className='md:mt-8 mt-5 '>
      <div>
      <div className="bg-white p-4 md:p-6 rounded-2xl font-poppins">
        <h2 className="text-xl font-semibold mb-2">What type of post ideas would you like quickAI to generate?</h2>
        <div className="bg-white border border-[#A1A2A9] rounded-xl mt-2">
          <textarea
            className="font-poppins md:text-lg text-sm text-gray_light leading-7 rounded-tl-2xl rounded-tr-2xl outline-none p-4 md:p-6 w-full"
            rows="4"
            placeholder="Enter the topics or keywords that you would like quickAI to generate post ideas about"
            value={topics}
            onChange={(e) => setTopics(e.target.value)}
          ></textarea>
        </div>
      
      <div className="flex justify-center items-center flex-col md:flex-row md:items-end md:justify-end  border-r-0.5 border-b-0.5 border-l-0.5 border-[#A1A2A9] gap-4 rounded-bl-2xl rounded-br-2xl bg-[#F5F7F9] p-4">
        <select className="p-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500">
          {Personas.map((data, index) => (
            <option key={index} value={data.persona_name}>{data.persona_name}</option>
          ))}
        </select>
        <button 
          className={`bg-gray-200 text-gray-800 px-4 py-2 rounded-md hover:bg-gray-300 focus:outline-none focus:ring-2 focus:ring-gray-500 ${loading ? 'opacity-50 cursor-not-allowed' : ''}`}
          onClick={getIdea}
          disabled={loading}
        >
          {loading ? 'Generating...' : 'Generate Ideas'}
        </button>
      </div>
      {/* button end */}
      </div>
      </div>
      </div>

      {generatedIdeas && (
        <div className="mt-4">
          <h3 className="text-lg font-bold mb-2">Generated Ideas:</h3>
          <p className="whitespace-pre-wrap mb-4">{generatedIdeas}</p>
          <button 
            className="bg-green-500 text-white px-4 py-2 rounded-md hover:bg-green-600 focus:outline-none focus:ring-2 focus:ring-green-500"
            onClick={copyToClipboard}
          >
            Copy to Clipboard
          </button>
        </div>
      )}
    </div>
  );
};

export default IdeaGenerator;