import React, { useEffect, useState } from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';
import axios from 'axios';
import { ToastContainer, toast } from "react-toastify";
import 'react-toastify/dist/ReactToastify.css';
import { useParams } from 'react-router-dom';

const Resetpassword = () => {
  const {reset_token} = useParams();
  const [password, setPassword] = useState("");
  const [confirmpassword, setconfirmpassword] = useState("");
  const [error, setError] = useState('');

  useEffect(()=>{
    console.log(reset_token)
  },[])

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
    try {
      const response = await axios.post(`http://localhost:3000/api/password/reset/${reset_token}`,{
        password : password,
        confirmPassword : confirmpassword
      })
      if (response.status === 200) {
        toast(response.data.message, {
          autoClose: 5000,
          position: "top-right",
        });
      }else{
        setError('Email address does not exist');
        toast.error(response.data.message, {
          autoClose: 5000,
          position: "top-right",
        });
      }
    }catch(err){
      setError("Error found");
    }
  };

  return (
    <div className="container mx-auto max-w-md mt-10">
      <div className="flex justify-between items-center mb-6">
        <h1 className="text-2xl font-semibold">RESET PASSWORD</h1>
        <div className="bg-gray-200 px-3 py-1 rounded-full text-sm">
          <a className='no-underline hover:no-underline text-black' href='/'>Home</a> &gt; Reset Password?
        </div>
      </div>
      <div className='flex justify-center'>
      <div className="bg-white shadow-md rounded px-8 pt-6 pb-8 mb-4 w-[40rem]">
        <h2 className="text-xl font-semibold mb-6">RESET?</h2>
        
        {error && (
          <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded relative mb-4" role="alert">
            <span className="block sm:inline">{error}</span>
          </div>
        )}
        <ToastContainer/>
        <form onSubmit={handleSubmit}>
          <div className="mb-4">
            <div>
                <div className="flex items-center border rounded-md my-4">
                  <i className="fas fa-lock mr-2 text-gray-400 px-3"></i>
                  <input
                    className="appearance-none border-none w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                    id="password"
                    type="password"
                    placeholder="Password"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    required
                  />
                </div>
            </div>
              <div>
                <div className="flex items-center border rounded-md">
                  <i className="fas fa-lock mr-2 text-gray-400 px-3"></i>
                  <input
                    className="appearance-none border-none w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                    id="confirm_password"
                    type="password"
                    placeholder="Confirm password"
                    value={confirmpassword}
                    onChange={(e) => setconfirmpassword(e.target.value)}
                    required
                  />
                </div>
              </div>
          </div>
          <div className="flex items-center justify-between">
            <button
              className="bg-green-500 hover:bg-green-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline w-full"
              type="submit"
            >
              Reset Password
            </button>
          </div>
        </form>
      </div>
      </div>
    </div>
  );
};

export default Resetpassword;