import React, { useEffect, useState } from 'react';
import Sidebar from '../CustomNav';
import axios from 'axios';
import ProfileCard from '../Additional/ProfileCard';
import Logoprofile from '../Additional/Logoprofile';

const getPostPlaceholder = ()=>{
  return(
    <div className="text-center">
      <h3 className="text-2xl font-bold mb-4">Save LinkedIn Profiles</h3>
      <p className="text-lg">
        We recommend creating a list of potential prospects, referral partners, and more! Keep yourself Top of Mind.
      </p>
    </div>
  );
}

function Profilepage() {
  const [postexists, set_post_existence] = useState(true);
  const [showModal, setShowModal] = useState(false);
  const [my_data ,setdata] = useState({url : "",fname : "",lname : ""})
  const [profiles,setprofiles] = useState([])

  useEffect(()=>{
    const getSavedProfiles = async()=>{
      try {
        const token = localStorage.getItem('token');
        const response = await axios.get('http://localhost:3000/api/getprofile', {
          headers: {
            'Accept': 'application/json, text/plain, */*',
            'Authorization': token
          }
        });
        setprofiles(response.data.message)
      }catch(error){
       console.log(error) 
      }
    }
    getSavedProfiles()
  },[])

  const handleChange = (event)=>{
    const {name,value} = event.target;
    setdata({...my_data,[name] : value})
  }

  const CardHolder = ()=>{
    return(
      <div className="p-4 bg-white rounded-lg shadow-md">
        <table className="min-w-full">
          <thead>
            <tr className="bg-gray-50">
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Name
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Lists
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Actions
              </th>
            </tr>
          </thead>
          {profiles.map((data,key)=>(
            <ProfileCard key={key} fname={data.fname} lname={data.lname} url={data.profile_URL}/>
          ))}
        </table>
        <div className="flex justify-between items-center mt-4">
          <div>
            <button className="px-4 py-2 bg-gray-200 text-gray-700 rounded-md">Prev</button>
            <button className="ml-2 px-4 py-2 bg-gray-200 text-gray-700 rounded-md">Next</button>
          </div>
          <div className="flex items-center">
            <span className="mr-2">Rows per page:</span>
            <select className="bg-white border border-gray-300 rounded-md py-2 pl-3 pr-10 text-base focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm">
              <option value="10">10</option>
              <option value="20">20</option>
              <option value="30">30</option>
            </select>
          </div>
        </div>
      </div>
    );
  }

  async function postProfile() {
    try {
        const token = localStorage.getItem('token');
        console.log(my_data.url);
        console.log(my_data.fname);
        console.log(my_data.lname);

        const response = await axios.post(
            'http://localhost:3000/api/profile',
            {
                profile_URL: my_data.url,
                fname: my_data.fname,
                lname: my_data.lname
            },
            {
                headers: {
                  'Accept': 'application/json, text/plain, */*',
                  'Authorization': token
                }
            }
        );
        if(response.status === 400){
          localStorage.removeItem('token');
          window.location.reload();
        }
        
        console.log(response.data);
        setShowModal(false);
        window.location.reload();
      } catch (error) {
        localStorage.removeItem('token');
        window.location.reload();
        console.error("An error occurred in PostProfile function under Profile Page:", error);
    }
}
  
    return (
      <div className='flex h-full bg-[#e2e8f0] min-h-screen'>
      <Sidebar className="w-1/5 p-4" />
      <div className="w-4/5 mx-auto px-4">
        <Logoprofile/>
        <div className="max-w-7xl mx-auto p-6">
          <div className="flex justify-between items-center mb-6">
            <h1 className="text-4xl font-bold">Saved Profiles</h1>
            <button
              onClick={() => setShowModal(true)}
              className="bg-green-500 text-white px-4 py-2 rounded-md flex items-center"
            >
              <span className="mr-2">+</span> Add New Profile
            </button>
          </div>
          <p className="text-lg mb-6">Easily create list(s) of profiles that you want to stay actively engaged with.</p>
          <div className="flex justify-between items-center mb-6">
            <div className="flex items-center space-x-4">
              <input
                type="text"
                placeholder="Search Profiles"
                className="px-4 py-2 border rounded-md w-72"
              />
              <div className="relative">
                <select className="block appearance-none w-full bg-white border border-gray-400 hover:border-gray-500 px-4 py-2 pr-8 rounded shadow leading-tight focus:outline-none focus:shadow-outline">
                  <option>Order By</option>
                  {/* Add more options here */}
                </select>
                <div className="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700">
                  <svg className="fill-current h-4 w-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20">
                    <path d="M7 10l5 5 5-5H7z" />
                  </svg>
                </div>
              </div>
              <div className="relative">
                <select className="block appearance-none w-full bg-white border border-gray-400 hover:border-gray-500 px-4 py-2 pr-8 rounded shadow leading-tight focus:outline-none focus:shadow-outline">
                  <option>Select List</option>
                  {/* Add more options here */}
                </select>
                <div className="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700">
                  <svg className="fill-current h-4 w-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20">
                    <path d="M7 10l5 5 5-5H7z" />
                  </svg>
                </div>
              </div>
              <a href="#" className="text-blue-600">Manage Lists</a>
            </div>
          </div>

          {postexists ? CardHolder() : getPostPlaceholder()}

          {showModal && (
            <div className="fixed inset-0 flex items-center justify-center z-50 bg-black bg-opacity-50">
              <div className="bg-white p-6 rounded-lg shadow-lg w-full max-w-lg">
                <div className="flex justify-between items-center mb-4">
                  <h2 className="text-2xl font-bold">Add New Profile</h2>
                  <button
                    onClick={() => setShowModal(false)}
                    className="text-gray-500 hover:text-gray-700"
                  >
                    &times;
                  </button>
                </div>
                <p className="mb-4">Please paste the LinkedIn profile URL you want to save in the 'Save Profile' section here.</p>
                <div className="mb-4">
                  <label className="block mb-2 text-sm font-medium text-gray-700">LinkedIn Profile URL</label>
                  <input
                    type="text"
                    name="url"
                    value={my_data.url}
                    placeholder="https://www.linkedin.com/in/"
                    className="w-full px-4 py-2 border rounded-md"
                    onChange={handleChange}
                  />
                </div>
                <div className="mb-4">
                  <label className="block mb-2 text-sm font-medium text-gray-700">First Name</label>
                  <input
                    type="text"
                    name="fname"
                    value={my_data.fname}
                    placeholder="e.g. Joe"
                    className="w-full px-4 py-2 border rounded-md"
                    onChange={handleChange}
                  />
                </div>
                <div className="mb-4">
                  <label className="block mb-2 text-sm font-medium text-gray-700">Last Name</label>
                  <input
                    type="text"
                    name="lname"
                    value={my_data.lname}
                    placeholder="e.g. Rogan"
                    className="w-full px-4 py-2 border rounded-md"
                    onChange={handleChange}
                  />
                </div>
                <div className="flex justify-end">
                  <button
                    onClick={postProfile}
                    className="bg-green-500 text-white px-4 py-2 rounded-md"
                  >
                    Save Profile
                  </button>
                </div>
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}

export default Profilepage;
