import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Content } from 'rsuite';

function Generator () {
  const goals = [
    "Ask a Question",
    "Celebrating Others",
    "Challenges and Solutions",
    "Common Myths",
    "Company Updates",
    "Educational",
    "Encourage Engagement",
    "Informational",
    "Introduction Post",
    "Networking",
    "Personal Story",
    "Poll",
    "Promotional",
    "Provide Value",
    "Seeking Advice",
    "Success Stories",
    "Thought Leadership",
  ];

  const tones = [
    "Analytical",
    "Assertive",
    "Authoritative",
    "Challenging",
    "Conversational",
    "Curious",
    "Educational",
    "Empathetic",
    "Friendly",
    "Funny",
    "Happy",
    "Informative",
    "Inspirational",
    "Metaphorical",
    "Motivational",
    "Narrative",
    "Normal",
    "Persuasive",
    "Philosophical",
    "Professional",
    "Reflective",
    "Sarcastic",
    "Sincere",
    "Visionary",
    "Whimsical",
  ];

  const [Personas ,setpersona] = useState([])
  const [goal, setGoal] = useState(" ");
  const [tone, setTone] = useState(" ");
  const [prompt, setPrompt] = useState("");
  const [loading, setLoading] = useState(false);
  const [generatedContent, setGeneratedContent] = useState("");
  const [selectedPersona, setSelectedPersona] = useState("");

  async function getdata(token ){
    // const singlePersona = await axios.get(`http://localhost:3000/api/getsinglepersona/${selectedPersona}`,{
    const singlePersona = await axios.get(`http://localhost:3000/api/getsinglepersona/${selectedPersona}`,{
      headers: {
        'Accept': 'application/json, text/plain, */*',
        'Authorization': token
      }
    });
    return (singlePersona.data.persona_detail[0]);
  }

  async function getcontent(persona_prompt,token){
      const complete_prompt = `Generate a post for linked , i am giving some parameters on basis of which you have to generate it. Goal : ${goal} , tone : ${tone} , my brief Persona : ${persona_prompt} and Prompt : ${prompt} , and dont give in markdown no extra symbols.`
      const created_by = localStorage.getItem("name");
    
      // const response = await axios.post("http://localhost:3000/api/gemni",{
      const response = await axios.post("http://localhost:3000/api/gemni",{
        prompt : complete_prompt,
        created_by : created_by
        ,
      },
      {
        headers: {
          'Accept': 'application/json, text/plain, */*',
          'Authorization': token
        }
      });
      return response.data.text;
      
  }
  
  async function sendData() {
    let token = localStorage.getItem('token');
    setLoading(true);
    try {
      let persona_response = await getdata(token);
      let persona_prompt = `This is my name ${persona_response.persona_name},this persona is for ${persona_response.tags}.I work in industry : ${persona_response.industry} , my desired tone : ${persona_response.tone_and_personality} , keywords i use to define myself are ${persona_response.keywords} , my background is ${persona_response.background}`;
      let content = await getcontent(persona_prompt, token);

      setPrompt(content);
      setGeneratedContent(content);
      
      // const response = await axios.post('http://localhost:3000/api/createpost', {
          const response = await axios.post('http://localhost:3000/api/createpost', {
        goal: goal,
        tone: tone,
        persona: persona_prompt,
        prompt: prompt,
        content: content
      }, {
        headers: {
          'Accept': 'application/json, text/plain, */*',
          'Authorization': token,
        }
      });
      
    } catch (error) {
      console.error(error);
    } finally {
      setLoading(false);
    }
  }

  function copyToClipboard() {
    navigator.clipboard.writeText(generatedContent).then(() => {
      alert("Content copied to clipboard!");
    }, (err) => {
      console.error('Could not copy text: ', err);
    });
  }

  useEffect(()=>{
    async function getpersona(){
      try{
        const token = localStorage.getItem('token');
        // const response = await axios.get('http://localhost:3000/api/getpersona', {
        const response = await axios.get('http://localhost:3000/api/getpersona', {
          headers: {
            'Accept': 'application/json, text/plain, */*',
            'Authorization': token
          }
        });
        console.log(response)
        setSelectedPersona(response.data.message[0].persona_name)
        console.log(response.data.message[0].persona_name)
        setpersona(response.data.message)
        // console.log(Personas)
        // all_personas = []
        }catch(error){
          console.error(error)
        }
    }
    getpersona()
  },[])

  return (
    <div>
    <div className='bg-white rounded-[16px] p-10 xsm:p-0 xsm:h- xsm:border border-[#A1A2A9] w-full md:mt-8 mt-5 '>
    <div className="xsm:h-full"> 
      {/* <div className="flex items-center justify-between mb-4"> */}
      <div className="xsm:hidden flex flex-col lg:flex-row items-center gap-8">
        {/* <div className="w-1/3"> */}
        <div className="flex flex-row items-center w-full gap-x-0">
          {/* <label className="block text-gray-700 font-bold mb-2" htmlFor="goal"> */}
          <label className="min-w-[80px] text-[#444553] font-poppins text-[22px] font-semibold " htmlFor="goal">
            Goal
          </label>
            <div className='custom-select'>
            <select
              id="goal"
              // className="block w-full bg-white border border-gray-400 hover:border-gray-500 px-4 py-2 pr-8 rounded shadow leading-tight focus:outline-none focus:shadow-outline"
              className="block cursor-pointer w-full text-base font-poppins outline-none text-gray_light border border-[#A1A2A9] !rounded-[10px] focus:border-[#A1A2A9] bg-transparent focus:ring-[#A1A2A9] p-3"
              onChange={(event)=>{
                setGoal(event.target.value)
              }}
              >
              {goals.map((goal, index) => (
                <option key={index} value={goal}>
                  {goal}
                </option>
              ))}
            </select>
            </div>
        </div>
        {/* <div className="w-1/3"> */}
        <div className=" flex items-center w-full gap-x-0">
          <label className="min-w-[80px] text-[#444553] font-poppins text-[22px] font-semibold " htmlFor="tone">
            Tone
          </label>
          <div className='custom-select'>

          <select
            id="tone"
            className="block cursor-pointer w-full  text-base outline-none text-gray_light font-poppins border border-[#A1A2A9] !rounded-[10px] focus:border-[#A1A2A9] bg-transparent focus:ring-[#A1A2A9] p-3"
            onChange={(event)=>{setTone(event.target.value)}}
            >
            {tones.map((tone, index) => (
              <option key={index} value={tone}>
                {tone}
              </option>
            ))}
          </select>
            </div>
        </div>
        {/* <div className="w-1/3"> */}
        <div className="flex items-center w-full gap-x-8">
          <label className="min-w-[80px] text-[#444553] font-poppins text-[22px] font-semibold " htmlFor="persona">
            Persona
          </label>
          <div className='custom-select'>

          <select
            id="persona" 
            className="block cursor-pointer w-full text-base font-poppins outline-none text-gray_light border border-[#A1A2A9] !rounded-[10px] focus:border-[#A1A2A9] bg-transparent focus:ring-[#A1A2A9] p-3"
            onChange={(event)=>{
              setSelectedPersona(event.target.value);
              console.log(selectedPersona)
            }}
            >
            {Personas.map((data, index) => (
              <option key={index} value={data.persona_name}>{data.persona_name}</option>
            ))}
          </select>
            </div>
        </div>
      </div>

      <div className="md:relative overflow-hidden relative xsm:h-[70vh] xsm:flex flex-col justify-between items-center mt-5 xsm:mt-0 xsm:px-0 md:border md:border-[#A1A2A9] md:rounded-lg md:outline-none md:pb-[72px]">
        <textarea
          id='prompt'
          className="outline-none block p-6 xsm:px-4 xsm:text-[14px] xsm:w-[99%] w-full font-poppins font-normal text-base text-[#121212] xsm:rounded-xl rounded-lg relative"
          rows="5"
          placeholder="Enter a prompt with instructions for the type of post you would like quickAI to create."
          onChange={(event) => setPrompt(event.target.value)}
          value={prompt}
        ></textarea>
        <div className='xsm:hidden font-poppins'>
            <button className='bg-[#F5F1FB] border-[0.7px] border-[#9778D21A] py-[2px] px-[3px] rounded-[6px] absolute top-[424px] right-[16px] md:top-[20px] md:right-[20px] flex gap-2'>
              <div className=''>

              </div>
            </button>
        </div>
      {/* <div className="flex items-center justify-between"> */}
      <div className="flex align-middle justify-between xsm:justify-center xsm:gap-6 md:absolute md:bottom-4 xsm:h-10 w-full md:pl-2 pr-4">
        <div className="flex items-center">
          <button 
            className={`bg-blue-500 text-white px-4 py-2 ml-2 rounded-md ${loading ? 'opacity-50 cursor-not-allowed' : ''}`} 
            onClick={sendData} 
            disabled={loading}
          >
            {loading ? 'Loading...' : 'Generate'}
          </button>
          {generatedContent && (
            <button 
              className="bg-green-500 text-white px-4 py-2 ml-2 rounded-md"
              onClick={copyToClipboard}
            >
              Copy to Clipboard
            </button>
          )}
        </div>
      </div>
      </div>


      {generatedContent && (
        <div className="mt-4">
          <h3 className="text-lg font-bold mb-2">Generated Content:</h3>
          <p className="whitespace-pre-wrap">{generatedContent}</p>
        </div>
      )}
    </div>
    </div>
    </div>
  );
};

export default Generator;
