import axios from 'axios';
import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { ToastContainer,toast } from "react-toastify";
import 'react-toastify/dist/ReactToastify.css';

const ForgotPassword = () => {
  const [email, setEmail] = useState('');
  const [error, setError] = useState('');
  const [loading,setloading] = useState(false)
  const navigate = useNavigate();

  const handleSubmit = async (e) => {
    e.preventDefault();
    setloading(true);
    setError('');
    try {
      // const response = await axios.post("http://localhost:3000/api/forgotpassword",{
      const response = await axios.post("http://localhost:3000/api/forgotpassword",{
        email : email
      })
      if (response.status !== 200) {
        setError('Email address does not exist');
        toast.error(response.data.message, {
          autoClose: 5000,
          position: "top-right",
        });
      } else {
        
        toast.success(response.data.message, {
          autoClose: 5000,
          position: "top-right",
        });
      }
    } catch (err) {
      setError('An error occurred. Please try again.');
    }
    finally{
      setloading(false);
    }
  };

  return (
    <div className="container mx-auto max-w-md mt-10">
      <div className="flex justify-between items-center mb-6">
        <h1 className="text-2xl font-semibold">Forgot Password?</h1>
        <div className="bg-gray-200 px-3 py-1 rounded-full text-sm">
          Home &gt; Forgot Password?
        </div>
      </div>
      <div className='flex justify-center'>
      <div className="bg-white shadow-md rounded px-8 pt-6 pb-8 mb-4 w-[40rem]">
        <h2 className="text-xl font-semibold mb-6">Forgot Password?</h2>
        
        {error && (
          <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded relative mb-4" role="alert">
            <span className="block sm:inline">{error}</span>
          </div>
        )}
        
        <form onSubmit={handleSubmit}>
          <div className="mb-4">
            <label className="block text-gray-700 text-sm font-bold mb-2" htmlFor="email">
              Email Address
            </label>
            <div className="flex items-center border rounded-md">
              <span className="text-gray-500 px-3">
                <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                  <path d="M2.003 5.884L10 9.882l7.997-3.998A2 2 0 0016 4H4a2 2 0 00-1.997 1.884z" />
                  <path d="M18 8.118l-8 4-8-4V14a2 2 0 002 2h12a2 2 0 002-2V8.118z" />
                </svg>
              </span>
              <input
                className="appearance-none border-none w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                id="email"
                type="email"
                placeholder="Email Address"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                required
              />
            </div>
          </div>
          <div className="flex items-center justify-between">
            <button
              className={`bg-green-500 hover:bg-green-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline w-full ${loading ? 'opacity-50 cursor-not-allowed' : ''}`}
              type="submit"
            >
              {loading?'Sending...':'Send Mail'}
            </button>
          </div>
        </form>
      </div>

    </div>
    </div>
  );
};

export default ForgotPassword;