import React from 'react'
import Sidebar from '../CustomNav';
import { useParams } from 'react-router-dom';
import { useLocation } from 'react-router';


function Post_page() {
    const location = useLocation();
    const { title, content } = location.state || {}; // Get the state passed with navigation
  
    if (!content) {
      return <div>Invalid Card Data</div>;
    }
  return (  
    <div className="flex xsm:rounded-[16px] xsm:py-2 md:px-0 xsm:mb-2 xsm:px-0 h-full">
        <Sidebar className="w-1/4 p-4" />
        <div className="w-3/4 mx-auto p-4">
            <h1 className="text-3xl font-bold mb-4">Post Highlight</h1>
            <div className="flex items-center justify-center h-screen bg-gray-100">
            <div className="bg-white shadow-md rounded p-4 mb-4 max-w-2xl w-full">
              <h2 className="text-2xl font-bold mb-4">{title}</h2>
              <p className="text-gray-700">{content}</p>
            </div>
          </div>
        </div>
    </div>
  )
}

export default Post_page