import './App.css';
import React, { Suspense} from 'react';
import { BrowserRouter, Route, Routes, Navigate } from 'react-router-dom';
import Login from './Component/login/Login';
import Home from './Component/home/Home';
import Postroute from './Component/Post/Post';
import Profilepage from './Component/Profile/Profilepage';
import Idea from './Component/Idea/Idea';
import Optimizer from './Component/Profile/Optimizer';
import Persona from './Component/Persona/Persona';
import AddPersona from './Component/Persona/Addpersona';
import Reg from './Component/Register/register';
import PrivateRoute from './Privateroute';
import Post_page from './Component/Post/Post_page';
import ForgotPassword from './Component/login/Forget';
import Updatepersona from './Component/Persona/Updatepersona';
import Resetpassword from './Component/login/Reset';
import Landing from './Component/Page/Landing';

function App() {
  return (
    <div className="App">
      <BrowserRouter>
        <Suspense fallback={<div>Loading...</div>}>
          <Routes>
            <Route path="/" element={<Landing />} />
            <Route path="/login" element={<Login />} />
            <Route path="/register" element={<Reg />} />
            <Route path='/forget' element={<ForgotPassword/>}/>
            <Route
              path="*"
              element={
                <PrivateRoute>
                  <Routes>
                    <Route path="/home" element={<Home />} />
                    <Route path="/post" element={<Postroute />} />
                    <Route path="/profile" element={<Profilepage />} />
                    <Route path="/idea" element={<Idea />} />
                    <Route path="/optimizer" element={<Optimizer />} />
                    <Route path="/personas" element={<Persona />} />
                    <Route path="/persona-add" element={<AddPersona />} />
                    <Route path="/card/:id" element={<Post_page />} />
                    <Route path="/edit_persona/:p_name" element={<Updatepersona />} />
                    <Route path="/passwordreset/:reset_token" element={<Resetpassword />} />
                    <Route path="*" element={<Navigate to="/home" replace />} />
                  </Routes>
                </PrivateRoute>
              }
            />
          </Routes>
        </Suspense>
      </BrowserRouter>
    </div>
  );
}
export default App;
