import React, { useState } from 'react'
import axios from 'axios';
import { useNavigate } from 'react-router-dom';

function PersonaCard({ id, persona_name, tags, isChecked, onDelete }) {
  const navigate = useNavigate()
  const [persona_name_todel,setpersonaname] = useState("")
  const handledelete = async () => {
    try {
      setpersonaname(document.getElementById('p_name').value)
      const token = localStorage.getItem('token');
      await axios.get(`http://localhost:3000/api/delete/${persona_name}`, {
        headers: {
          'Accept': 'application/json, text/plain, */*',
          'Authorization': token
        }
      });
      console.log(persona_name)
      window.location.reload()

    } catch (error) {
      console.error("Error deleting persona:", error);
    }
  }
  const handleedit = async () => {
    const p_name = persona_name;
    navigate(`/edit_persona/${p_name}`,{state:{p_name}})
  }

  return (
    <tr className="border-b">
      <td className="py-2 px-4 w-12">
        <input 
          type="checkbox" 
          className="form-checkbox h-5 w-5 text-indigo-600" 
          checked={isChecked}
        />
      </td>
      <td className="py-2 px-4">
        <div className="flex items-center">
          <div className="bg-gray-200 rounded-full w-8 h-8 flex items-center justify-center mr-2">
            <span className="text-gray-600 text-sm">{persona_name.substring(0, 2).toLowerCase()}</span>
          </div>
          <span id='p_name'>{persona_name}</span>
        </div>
      </td>
      <td className="py-2 px-4">
        <span className="text-indigo-600">{tags}</span>
      </td>
      <td className="py-2 px-4 text-right">
        <button className="text-blue-600 pr-1 hover:text-blue-800" onClick={handledelete}><i className='fas fa-trash'/></button>
        <button className="text-blue-600 pl-1 hover:text-blue-800" onClick={handleedit}><i class="fas fa-pen"></i></button>
      </td>
    </tr>
  )
}

export default PersonaCard;
