import React, { useEffect, useState } from 'react';
import Card from './card';
import Sidebar from '../CustomNav';
import axios from 'axios';
// import { Link } from 'react-router-dom';
import Logoprofile from '../Additional/Logoprofile';

function Home() {
  const [posts, setPosts] = useState([]);
  const [loading, setLoading] = useState(true);
  const [displayCount, setDisplayCount] = useState(4);

  function renderpost(){
    return (posts.slice(0, displayCount).map((post) => (
      <Card key={post._id} title={post.title} content={post.content} id={post._id}/>
    )))
  }
  function renderNopost(){
    return (
      <div className='flex justify-center'>
        <div className='font-sans bg-white shadow-md rounded px-8 pt-6 pb-8 mb-4 w-auto p-20'>
          <h3>
          You have No Recent Activity of Posts.
          </h3>
        </div>
      </div>
    );
  }

  useEffect(() => {
    const func = async()=>{

      let token = localStorage.getItem('token');
      const fetchPosts = async () => {
        try {
          const response = await axios.get('http://localhost:3000/api/getallpost', {
            headers: {
              'Accept': 'application/json, text/plain, */*',
              'Authorization': token
            }
          });
          if(response.status === 400){
            localStorage.removeItem('token');
            window.location.reload();
          }
          setPosts(response.data.post);
        } catch (error) {
          localStorage.removeItem('token');
          window.location.reload();
          console.error('There was an error fetching the posts!', error);
        } finally {
          setLoading(false);
        }
      };
  
      fetchPosts();
    }
    func();
  }, []);

  if (loading) {
    return <div>Loading...</div>;
  }

  return (
    <div className="flex flex-row h-full bg-[#e2e8f0] relative min-h-screen">
      <Sidebar className="w-1/5 pt-4" />
      {/* <div className="w-5/6 mx-1 py-4 px-3"> */}
      <div className="w-4/5 mx-auto px-4">
        <Logoprofile/>
        <div className='flex md:items-end items-center justify-between flex-wrap'>
          <h3 className="text-4xl font-bold">Posts</h3>
          <button onClick={()=>{window.location = "/post"}} className='bg-green-500 text-white rounded-md pb-2 px-4 py-2'><i className="fas fa-plus "></i> New Post</button>
        </div>
        <p className='custom-primary-p-style'>Browse your recent activities as well as any items you have saved to view or use later.</p>
        <div className='md:mt-8 mt-4 mb-4 flex justify-between items-center md:pr-0 pr-3 flex-wrap'>
          <div className='flex items-center flex-wrap gap-2'>
            <span className='font-poppins text-black md:text-black_color md:text-[20px] md:font-semibold text-lg font-bold md:leading-[30px]'><i className="fas fa-clock "></i> Recent Activities</span>
          </div>
          {posts.length > displayCount && (
            <button className="flex items-center justify-between flex-wrap " onClick={()=>{setDisplayCount(posts.length)}}>
              <span className='font-poppins text-base font-bold text-secondry_color underline'>
              {"See all >"}
              </span>
              </button>
          )}
          {posts.length === displayCount && (
            <button className="flex items-center justify-between flex-wrap " onClick={()=>{setDisplayCount(4)}}>
              <span className='font-poppins text-base font-bold text-secondry_color underline'>
                {"See less <"}
              </span>
              </button>
          )}

        </div>
        <div className='your_post_div'>
        <div className='gap-4 mt-6 mb-10 !z-0'>
          <div className="flex flex-wrap ">
              {displayCount === 0 ?renderNopost():renderpost()}
          </div>
        </div>
        </div>
      </div>
    </div>

  );
}

export default Home;
