import React, { useState } from "react";
import axios from "axios";
import { Link } from "react-router-dom";
import Cookies from 'js-cookie'

const Login = () => {
    const [data, setData] = useState({ name: "", email: "", password: "" });
    const [error, setError] = useState("");
    const [loading, setLoading] = useState(false);

    function setLoginCookie() {
        // chrome.storage.sync.set({'foo': 'hello', 'bar': 'hi'}, function() {
        //     console.log('Settings saved');
        //   });
        Cookies.set('auth_token', data.token, { expires: 1, secure: true, sameSite: 'Strict' });
    }

    const handleChange = (e) => {
        const { name, value } = e.target;
        setData({ ...data, [name]: value });
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        setLoading(true);
        try {
            const url = "http://localhost:3000/api/login";
            const { data: res } = await axios.post(url, data);
            console.log(res.token);
            setLoginCookie();
            localStorage.setItem("token", res.token);
            localStorage.setItem("name", res.user.name);
            localStorage.setItem("email", res.user.email);
            window.location = "/home";
        } catch (error) {
            if (
                error.response &&
                error.response.status >= 400 &&
                error.response.status <= 500
            ) {
                setError(error.response.data.message);
            }
        } finally {
            setLoading(false);
        }
    };

    return (
        <div className="min-h-screen flex items-center justify-center bg-gray-100">
            <div className="bg-white p-8 rounded shadow-md w-full max-w-md">
                <div className="card-body">
                    <div className="text-center mb-5">
                        <h1 className="text-3xl font-bold">Login</h1>
                        <p className="text-gray-600">
                            Don't have an account? <Link to="/register" className="text-blue-500">Sign Up Now!</Link>
                        </p>
                    </div>

                    <div className="flex items-center my-4">
                        <div className="flex-grow h-px bg-gray-300"></div>
                        <span className="flex-shrink mx-4 text-gray-400">or</span>
                        <div className="flex-grow h-px bg-gray-300"></div>
                    </div>
                    <form onSubmit={handleSubmit}>
                        <div className="mb-4">
                            <label className="input input-bordered flex items-center">
                                <i className="fas fa-user mr-2 text-gray-400"></i>
                                <input
                                    type="text"
                                    placeholder="Email"
                                    name="email"
                                    value={data.email}
                                    onChange={handleChange}
                                    className="w-full px-4 py-2 border rounded focus:outline-none focus:ring-2 focus:ring-blue-600"
                                    required
                                />
                            </label>
                        </div>
                        <div className="mb-4">
                            <label className="input input-bordered flex items-center">
                                <i className="fas fa-lock mr-2 text-gray-400"></i>
                                <input
                                    type="password"
                                    placeholder="Password"
                                    name="password"
                                    value={data.password}
                                    onChange={handleChange}
                                    className="w-full px-4 py-2 border rounded focus:outline-none focus:ring-2 focus:ring-blue-600"
                                    required
                                />
                            </label>
                        </div>
                        <div className="flex justify-between items-center mb-4">
                            <Link to="/forget" className="text-blue-500">Forgot Password?</Link>
                        </div>
                        <button type="submit" className="btn w-full btn-primary" disabled={loading}>
                            {loading ? "Signing in..." : "Login"}
                        </button>
                    </form>
                    {error && <div className="text-red-500 mt-4">{error}</div>}
                </div>
            </div>
        </div>
    );
};

export default Login;
