import React, { useState } from 'react';
import axios from 'axios';
import Sidebar from '../CustomNav';

const AddPersona = () => {
  const [personaName, setPersonaName] = useState('');
  const [selectedTag, setSelectedTag] = useState('Person');
  const [industry, setIndustry] = useState('');
  const [tone, setTone] = useState('Analytical');
  const [keywords, setKeywords] = useState('');
  const [background, setBackground] = useState('');


  async function postProfile(){
    const token = localStorage.getItem('token');
    try{
      await axios.post('http://localhost:3000/api/createpersona',{
        persona_name : personaName,
        tags : selectedTag,
        industry : industry,
        tone_and_personality : tone,
        keywords : keywords,
        background : background,
        headers: {
          'Accept': 'application/json, text/plain, */*',
          'Authorization': token,
        }
      })
      .then((data)=>{
        console.log("I am the best");
        console.log(data);
      })
      window.location = '/personas'
    }
    catch(error){
      console.log(error)
    }
  }



  return (
    <div className="flex h-full bg-[#e2e8f0] min-h-screen">
      <Sidebar className="w-1/4 p-4" />
      <div className="w-3/4 container mx-auto p-4">
    <div className="max-w-6xl mx-auto p-6 bg-white shadow-lg rounded-lg">
      <h1 className="text-2xl font-bold mb-4">Add persona</h1>
      <p className="text-blue-600 mb-6 cursor-pointer hover:underline">
        Click here to learn how to fill in your Background and History
      </p>

      <div className="flex mb-6">
        <div className="bg-yellow-300 w-16 h-16 rounded-lg flex items-center justify-center mr-4">
          <svg className="w-10 h-10 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 4.354a4 4 0 110 5.292M15 21H3v-1a6 6 0 0112 0v1zm0 0h6v-1a6 6 0 00-9-5.197M13 7a4 4 0 11-8 0 4 4 0 018 0z"></path>
          </svg>
        </div>
        <div className="flex-grow">
          <label className="block text-sm font-medium text-gray-700 mb-1">
            Persona Name <span className="text-red-500">*</span>
          </label>
          <input
            type="text"
            className="w-full px-3 py-2 border border-gray-300 rounded-md"
            placeholder="Ex: Ajax Union"
            value={personaName}
            onChange={(e) => setPersonaName(e.target.value)}
            />
        </div>
      </div>

      <div className="mb-6">
        <label className="block text-sm font-medium text-gray-700 mb-1">
          Tags <span className="text-red-500">*</span>
        </label>
        <div className="flex space-x-4">
          <button
            className={`px-4 py-2 rounded-full ${selectedTag === 'Person' ? 'bg-blue-100 text-blue-800' : 'bg-gray-100 text-gray-800'}`}
            onClick={() => setSelectedTag('Person')}
            >
            Person
          </button>
          <button
            className={`px-4 py-2 rounded-full ${selectedTag === 'Company' ? 'bg-blue-100 text-blue-800' : 'bg-gray-100 text-gray-800'}`}
            onClick={() => setSelectedTag('Company')}
            >
            Company
          </button>
        </div>
      </div>

      <div className="grid grid-cols-2 gap-6 mb-6">
        <div>
          <label className="block text-sm font-medium text-gray-700 mb-1">Industry</label>
          <input
            type="text"
            className="w-full px-3 py-2 border border-gray-300 rounded-md"
            placeholder="Industry"
            value={industry}
            onChange={(e) => setIndustry(e.target.value)}
            />
        </div>
        <div>
  <label className="block text-sm font-medium text-gray-700 mb-1">Tone and Personality</label>
  <select
    className="w-full px-3 py-2 border border-gray-300 rounded-md"
    value={tone}
    onChange={(e) => setTone(e.target.value)}
  >
    <option value="Analytical">Analytical</option>
    <option value="Assertive">Assertive</option>
    <option value="Authoritative">Authoritative</option>
    <option value="Challenging">Challenging</option>
    <option value="Conversational">Conversational</option>
    <option value="Curious">Curious</option>
    <option value="Educational">Educational</option>
    <option value="Empathetic">Empathetic</option>
    <option value="Friendly">Friendly</option>
    <option value="Funny">Funny</option>
    <option value="Happy">Happy</option>
    <option value="Informative">Informative</option>
    <option value="Inspirational">Inspirational</option>
    <option value="Metaphorical">Metaphorical</option>
    <option value="Motivational">Motivational</option>
    <option value="Narrative">Narrative</option>
    <option value="Normal">Normal</option>
    <option value="Persuasive">Persuasive</option>
    <option value="Philosophical">Philosophical</option>
    <option value="Professional">Professional</option>
    <option value="Reflective">Reflective</option>
    <option value="Sarcastic">Sarcastic</option>
    <option value="Sincere">Sincere</option>
    <option value="Visionary">Visionary</option>
    <option value="Whimsical">Whimsical</option>
  </select>
</div>
      </div>

      <div className="mb-6">
        <label className="block text-sm font-medium text-gray-700 mb-1">Keywords and Phrases</label>
        <input
          type="text"
          className="w-full px-3 py-2 border border-gray-300 rounded-md"
          placeholder="Ex: Fast-growing"
          value={keywords}
          onChange={(e) => setKeywords(e.target.value)}
          />
      </div>

      <div className="mb-6">
        <label className="block text-sm font-medium text-gray-700 mb-1">
          Background and History <span className="text-red-500">*</span>
        </label>
        <textarea
          className="w-full px-3 py-2 border border-gray-300 rounded-md"
          rows="6"
          placeholder="Enter in bullet points or a short summary about this persona, up to 2,000 characters."
          value={background}
          onChange={(e) => setBackground(e.target.value)}
          ></textarea>
        <div className="text-right text-sm text-gray-500 mt-1">
          {background.length} / 2000
        </div>
      </div>

      <div className="flex justify-end space-x-4">
        <button className="px-6 py-2 bg-indigo-600 text-white rounded-md hover:bg-indigo-700" onClick={postProfile}>
          Save Persona
        </button>
        <button className="px-6 py-2 border border-gray-300 text-gray-700 rounded-md hover:bg-gray-50">
          Delete Persona
        </button>
      </div>
    </div>
    </div>        
    </div>
  );
};

export default AddPersona;