import { Sidenav, Nav } from 'rsuite';
import DashboardIcon from '@rsuite/icons/legacy/Dashboard';
import MagicIcon from '@rsuite/icons/legacy/Magic';
import GearCircleIcon from '@rsuite/icons/legacy/GearCircle';
import GroupIcon from '@rsuite/icons/legacy/Group';
import FileTextIcon from '@rsuite/icons/legacy/FileText';
import CreativeIcon from '@rsuite/icons/Creative'
import BookIcon from '@rsuite/icons/legacy/Book';
import PeoplesIcon from '@rsuite/icons/legacy/Peoples';
import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import './customnav.css'
import quickAI from '../Component/Additional/QuickAI-Image.png'

const Sidebar = () => {
  const location = useLocation();

  const isActive = (path) => {
    return location.pathname === path;
  };

  return (
    <div className="sidebar-container">
      <Sidenav>
        <Sidenav.Body className="sidenav-body">
          <Nav>
            <Nav.Item className="image-container">
              <img src={quickAI} alt="Quick AI" />
            </Nav.Item>
            <Nav.Item 
              icon={<DashboardIcon />} 
              as={Link} 
              to="/home"
              className={isActive('/home') ? 'active-nav-item' : ''}
            >
              Home
            </Nav.Item>
            <Nav.Item 
              icon={<GroupIcon />}
              as={Link} 
              to="/profile"
              className={isActive('/profile') ? 'active-nav-item' : ''}
            >
              Saved Profile
            </Nav.Item>
            <Nav.Item 
              icon={<FileTextIcon />}
              as={Link} 
              to="/post"
              className={isActive('/post') ? 'active-nav-item' : ''}
            >
              Post Writer
            </Nav.Item>
            <Nav.Item 
              icon={<CreativeIcon/>}
              as={Link} 
              to="/idea"
              className={isActive('/idea') ? 'active-nav-item' : ''}
            >
              Idea Generator
            </Nav.Item>
            {/* <Nav.Item 
              icon={<MagicIcon />}
              as={Link} 
              to="/templates"
              className={isActive('/templates') ? 'active-nav-item' : ''}
            >
              Templates
            </Nav.Item> */}
            <Nav.Item 
              icon={<GearCircleIcon />}
              as={Link} 
              to="/optimizer"
              className={isActive('/optimizer') ? 'active-nav-item' : ''}
            >
              Profile Optimizer
            </Nav.Item>
            <Nav.Item 
              icon={<GroupIcon />}
              as={Link} 
              to="/personas"
              className={isActive('/personas') ? 'active-nav-item' : ''}
            >
              Personas
            </Nav.Item>
            <Nav.Item 
              icon={<BookIcon />}
              as={Link} 
              to="/learning-hub"
              className={isActive('/learning-hub') ? 'active-nav-item' : ''}
            >
              Learning Hub
            </Nav.Item>
            <Nav.Item 
              icon={<PeoplesIcon />}
              as={Link} 
              to="/community"
              className={isActive('/community') ? 'active-nav-item' : ''}
            >
              Community
            </Nav.Item>
            <Nav.Item 
              as={Link} 
              to="/demo"
              className={isActive('/demo') ? 'active-nav-item' : ''}
            >
              Demo
            </Nav.Item>
          </Nav>
        </Sidenav.Body>
      </Sidenav>
    </div>
  );
};
export default Sidebar;


